body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.side{display:flex;flex-direction:column;height:100vh;overflow:hidden;background-color:#dcdcdc}ul{display:flex;flex-direction:column;justify-content:space-evenly;height:80vh;margin:0;padding:0}ul :hover{color:#000000;background-color:#ffffff}.imagem{margin:3em auto;border-radius:50%;width:10em}.icon{width:1em;height:1em;margin-right:1em}.social-icon{width:1.5em;height:1.5em;margin-right:2em;margin-top:1em}.grow{transition:all 0.2s ease-in-out}.grow :hover{rotate:3;transform:scale(1.5)}.contacts{display:flex;flex-direction:column;align-items:center;justify-content:center}.contacts img:hover{transform:translate(10px, -10px)}h1{margin-bottom:0.5em !important;size:65px;font-family:'Open Sans', sans-serif;font-weight:700;text-transform:uppercase}a{color:black !important;text-decoration:none !important}

*{font-family:'Merriweather', serif}.profile-image{position:absolute;z-index:0;width:80vw;height:100vh;object-fit:cover;object-position:100% 0}.dashboard{display:flex}.about-me{background-color:rgba(255,255,255,0.3);z-index:1;border-radius:6px;margin-top:50vh;margin-left:5vw}.about-me h1{font-size:5em;text-align:left;font-weight:800;margin-bottom:0.1em}

*{box-sizing:border-box;line-height:1.5;font-family:'Open Sans', sans-serif}.category{height:10vh}img{max-width:100%}.container{display:flex;align-items:center;justify-content:center;margin:auto !important;overflow:hidden;transition:all 0.2s ease-in-out}.container :hover{transform:scale(1.01)}.card{margin:30px 30px;position:relative;background:#333;width:450px !important;height:300px;border-radius:6px;color:#aaa;box-shadow:0 0.25rem 0.25rem rgba(0,0,0,0.2),0 0 1rem rgba(0,0,0,0.2);overflow:hidden}.card__image-container{margin:-35px}.card__line{opacity:0;-webkit-animation:LineFadeIn 0.8s 0.8s forwards ease-in;animation:LineFadeIn 0.8s 0.8s forwards ease-in}.card__image{opacity:0;-webkit-animation:ImageFadeIn 0.8s 1.4s forwards;animation:ImageFadeIn 0.8s 1.4s forwards}.card__title{color:white;width:100%;margin-top:5px;margin-bottom:15px !important;font-size:40px;font-weight:800;letter-spacing:0.01em}.card__content{margin-top:-1rem;opacity:0;-webkit-animation:ContentFadeIn 0.8s 1.6s forwards;animation:ContentFadeIn 0.8s 1.6s forwards;padding:5px}.card__svg{position:absolute;left:0;top:-60px}@-webkit-keyframes LineFadeIn{0%{opacity:0;d:path("M 0 300 Q 0 300 0 300 Q 0 300 0 300 C 0 300 0 300 0 300 Q 0 300 0 300 ");stroke:#fff}50%{opacity:1;d:path("M 0 300 Q 50 300 100 300 Q 250 300 350 300 C 350 300 500 300 650 300 Q 750 300 800 300");stroke:#888bff}100%{opacity:1;d:path("M -2 100 Q 50 200 100 250 Q 250 400 350 300 C 400 250 550 150 650 300 Q 750 450 802 400");stroke:#545581}}@keyframes LineFadeIn{0%{opacity:0;d:path("M 0 300 Q 0 300 0 300 Q 0 300 0 300 C 0 300 0 300 0 300 Q 0 300 0 300 ");stroke:#fff}50%{opacity:1;d:path("M 0 300 Q 50 300 100 300 Q 250 300 350 300 C 350 300 500 300 650 300 Q 750 300 800 300");stroke:#888bff}100%{opacity:1;d:path("M -2 100 Q 50 200 100 250 Q 250 400 350 300 C 400 250 550 150 650 300 Q 750 450 802 400");stroke:#545581}}@-webkit-keyframes ContentFadeIn{0%{transform:translateY(-1rem);opacity:0}100%{transform:translateY(0);opacity:1}}@keyframes ContentFadeIn{0%{transform:translateY(-1rem);opacity:0}100%{transform:translateY(0);opacity:1}}@-webkit-keyframes ImageFadeIn{0%{transform:translate(-0.5rem, -0.5rem) scale(1.05);opacity:0;-webkit-filter:blur(2px);filter:blur(2px)}50%{opacity:1;-webkit-filter:blur(2px);filter:blur(2px)}100%{transform:translateY(0) scale(1);opacity:1;-webkit-filter:blur(0);filter:blur(0)}}@keyframes ImageFadeIn{0%{transform:translate(-0.5rem, -0.5rem) scale(1.05);opacity:0;-webkit-filter:blur(2px);filter:blur(2px)}50%{opacity:1;-webkit-filter:blur(2px);filter:blur(2px)}100%{transform:translateY(0) scale(1);opacity:1;-webkit-filter:blur(0);filter:blur(0)}}.profile-image2{position:absolute;z-index:-1}.content{background-image:url("/background2.jpg");background-repeat:no-repeat;background-size:cover;background-color:darkgray;background-blend-mode:screen;width:80vw}


.academic{display:flex;height:30vh;margin:2em}.academic .imglist{width:70%;height:30vh;-webkit-column-count:3;column-count:3;padding-top:1em;padding-left:2em}.academic .academic_image{border-right:1px solid grey;width:30%}.academic .academic_image img{width:15em;height:15em;padding:2em}.academic li{display:flex;flex-direction:column;justify-content:center;padding:10 0 10 24px;height:40vh;list-style:none;background-image:url("/check.png");background-repeat:no-repeat;background-position:left center;background-size:20px}.tasks{width:70%;height:30vh;-webkit-column-count:3;column-count:3;padding-top:1em;padding-left:2em;display:flex;flex-direction:column}.singleTask{display:flex;margin-top:0.5em;margin-bottom:2em}.singleTask img{width:35px;margin-right:2em}.singleTask img :hover{-webkit-animation:skew 3s infinite;animation:skew 3s infinite}h2{margin-top:5em}

.title{margin-top:1.5em;font-weight:700;text-decoration:underline}.skills{height:265px;padding-left:2em;columns:4;-webkit-columns:4;-moz-columns:4}.interesses{height:200px;padding-top:1em;padding-left:2em;columns:3;-webkit-columns:3;-moz-columns:3}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

